<template>
  <div class="upload">
    <div class="upload-main">
      <input type="file" :accept="accept" @change="handlerFileChange" :multiple="multiple && 'multiple'"/>
      <slot name="select-btn">
        <div class="btn">
          <button></button>
        </div>
      </slot>
    </div>
    <ul class="upload-list">
      <li
        v-for="(file, index) in fileList"
        :key="file.name"
      >
        <img :src="file.preview" v-if="file.preview"/>
        <label>
          ({{getSize(file.size)}})
          {{file.name}}
        </label>
        <span
          @click="handlerRemoveFile(file, index)"
          :class="{
            error: (file.status === UPLOAD_FAIL || !!file.msg),
            success: file.status === UPLOAD_SUCCESS
          }"
        >
          {{file.msg || (file.per? `${file.per}%`: '')}}
        </span>
        <i
          :style="{ width: `${file.per || 0}%` }"
          :class="{
            error: file.status === UPLOAD_FAIL,
            success: file.status === UPLOAD_SUCCESS
          }"
        />
      </li>
    </ul>
    <button
      v-if="fileList.some(f => [READY_UPLOAD, UPLOAD_FAIL].includes(f.status))"
      @click="handlerUploading"
    >
      
    </button>
  </div>
</template>
<script>
import axios from 'axios'
const requestCancelQueue = {} // 
const VALIDATE_FAIL = -1 // 
const READY_UPLOAD = 0 // 
const UPLOADING = 1 // 
const UPLOAD_FAIL = 2 // 
const UPLOAD_SUCCESS = 3 // 
export default {
  name: 'upload-app',
  props: {
    multiple: {
      type: Boolean,
      default: true
    },
    // input[type="file"] accept
    accept: {
      type: String,
      default: 'image/*'
    },
    // previewstatus=UPLOAD_SUCCESS
    fileList: {
      type: Array,
      default: () => []
    },
    // 
    maxSize: {
      type: Number,
      default: 5 * 1024 * 1024 * 1024
    },
    // ()
    multiUploadSize: {
      type: Number,
      default: 100 * 1024 * 1024
    },
    // 
    eachSize: {
      type: Number,
      default: 50 * 1024 * 1024
    },
    postUrl: {
      type: String,
      default: 'http://localhost:8888'
    },
    validateUrl: {
      type: String,
      default: 'http://localhost:8888/validateFile'
    }
  },
  data () {
    return {
      READY_UPLOAD,
      UPLOADING,
      UPLOAD_FAIL,
      UPLOAD_SUCCESS
    }
  },
  methods: {
    handlerFileChange ({ target: { files } }) {
      const { fileList, maxSize, getImgBlob } = this
      Array.prototype.map.call(files, file => {
        if (!fileList.some(f => f.name === file.name)) {
          file.preview = getImgBlob(file)
          file.status = READY_UPLOAD
          if (file.size > maxSize) {
            file.status = VALIDATE_FAIL
            file.msg = `${this.getSize(maxSize)}`
          }
          fileList.push(file)
        }
      })
    },
    // 
    handlerUploading () {
      const { fileList, multiUploadSize, splitUpload, singleUpload, setFileData } = this
      fileList.map(file => {
        if ([READY_UPLOAD, UPLOAD_FAIL].includes(file.status)) {
          const uploadFunc = file.size > multiUploadSize ? splitUpload : singleUpload
          setFileData(file.name, {
            per: 0,
            msg: null,
            status: UPLOADING
          })
          uploadFunc(file)
        }
      })
    },
    // 
    async singleUpload (file) {
      const { setFileData } = this
      try {
        await this.postFile({ file, fileName: file.name })
        setFileData(file.name, 'status', UPLOAD_SUCCESS)
      } catch (e) {
        setFileData(file.name, {
          per: 0,
          msg: e,
          status: UPLOAD_FAIL
        })
      }
    },
    // 
    async splitUpload (file) {
      const { eachSize, setFileData } = this
      try {
        const chunks = Math.ceil(file.size / eachSize)
        const fileChunks = await this.splitFile(file, eachSize, chunks)
        let currentChunk = 0
        for (let i = 0; i < fileChunks.length; i++) {
          // currentChunkcurrentChunk
          if (Number(currentChunk) === i) {
            // index
            currentChunk = await this.postFile({
              chunked: true,
              chunk: i,
              chunks,
              eachSize,
              fileName: file.name,
              fullSize: file.size,
              file: fileChunks[i]
            })
          }
        }
        setFileData(file.name, 'per', 99.99)
        const isValidate = await this.validateFile({
          chunks: fileChunks.length,
          fileName: file.name,
          fullSize: file.size
        })
        if (!isValidate) {
          throw new Error('')
        }
        setFileData(file.name, {
          per: 100,
          status: UPLOAD_SUCCESS
        })
      } catch (e) {
        setFileData(file.name, {
          per: 0,
          msg: e,
          status: UPLOAD_FAIL
        })
      }
    },
    // 
    getSize (size) {
      return size > 1024
        ? size / 1024 > 1024
          ? size / (1024 * 1024) > 1024
            ? (size / (1024 * 1024 * 1024)).toFixed(2) + 'GB'
            : (size / (1024 * 1024)).toFixed(2) + 'MB'
          : (size / 1024).toFixed(2) + 'KB'
        : (size).toFixed(2) + 'B'
    },
    // ,Array.prototype.slice
    splitFile (file, eachSize, chunks) {
      return new Promise((resolve, reject) => {
        try {
          setTimeout(() => {
            const fileChunk = []
            for (let chunk = 0; chunks > 0; chunks--) {
              fileChunk.push(file.slice(chunk, chunk + eachSize))
              chunk += eachSize
            }
            resolve(fileChunk)
          }, 0)
        } catch (e) {
          reject(new Error(''))
        }
      })
    },
    handlerRemoveFile (file, index) {
      if (requestCancelQueue[file.name]) {
        requestCancelQueue[file.name]()
        delete requestCancelQueue[file.name]
      }
      this.fileList.splice(index, 1)
    },
    // ,FormData, axios
    postFile (param) {
      const formData = new FormData()
      for (let p in param) {
        formData.append(p, param[p])
      }
      const { fileName } = param
      const config = {
        cancelToken: new axios.CancelToken(function (cancel) {
          if (requestCancelQueue[fileName]) {
            requestCancelQueue[fileName]()
            delete requestCancelQueue[fileName]
          }
          requestCancelQueue[fileName] = cancel
        }),
        onUploadProgress: e => {
          let percent = 0
          if (param.chunked) {
            percent = Number(((((param.chunk * (param.eachSize - 1)) + (e.loaded)) / param.fullSize) * 100).toFixed(2))
          } else {
            percent = Number(((e.loaded / e.total) * 100).toFixed(2))
          }
          this.setFileData(fileName, 'per', percent)
        }
      }
      return axios.post(this.postUrl, formData, config).then(rs => rs.data)
    },
    // 
    validateFile (file) {
      return axios.post(this.validateUrl, file).then(rs => rs.data)
    },
    getImgBlob (file) {
      return /\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i.test(file.name) ? window.URL.createObjectURL(file) : null
    },
    setFileData (fileName, key, value) {
      const index = this.fileList.findIndex(f => f.name === fileName)
      const file = this.fileList[index]
      if (!file) return
      if (typeof key === 'string') {
        file[key] = value
      } else {
        for (let k in key) {
          file[k] = key[key]
        }
      }
      this.$set(this.fileList, index, file)
    }
  }
}
</script>
<style lang="less" scoped>
.upload{
  width: 100%;
  height: 100%;
  &-main{
    position: relative;
    display: inline-block;
    input[type="file"]{
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      opacity: 0;
      cursor: pointer;
    }
    .btn{
      text-align: center;
    }
  }
  &-list{
    list-style: none;
    padding: 10px;
    li{
      padding: 10px 0;
      display: flex;
      align-items: center;
      position: relative;
      img{
        width: 40px;
        height: 40px;
      }
      label{
        padding-left: 10px;
        flex: 1;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap
      }
      span{
        padding-right: 20px;
        height: 20px;
        position: relative;
        cursor: pointer;
        transition: transform .3s;
        .icon{
          content: '';
          position: absolute;
          top: 2px;
          right: 4px;
          width: 2px;
          height: 12px;
          background: #cccccc;
        }
        &:before{
          .icon;
          transform: rotate(45deg);
        }
        &:after{
          .icon;
          transform: rotate(-45deg);
        }
        &:hover{
          transform: scale(1.2);
        }
        &.error{
          color: #ed4014;
        }
        &.success{
          color: #19be6b;
        }
      }
      i{
        position: absolute;
        bottom: 4px;
        left: 0;
        display: block;
        height: 5px;
        border-radius: 5px;
        transition: all .3s;
        opacity: .8;
        background: #2D8cF0;
        &.error{
          background: #ed4014;
        }
        &.success{
          background: #19be6b;
        }
      }
    }
  }
  button{
    background: #2D8cF0;
    width: 100%;
    padding: 5px;
    border: 0;
    border-radius: 5px;
    color:#fff;
    cursor: pointer;
  }
}
</style>
